<template>
  <div class="musica">
    <v-container>
      <carousel-header :phrase="frase_musica"></carousel-header>
      <player-title-bar></player-title-bar>
      <player-info-panel :trackInfo="getTrackInfo"></player-info-panel>
      <player-controls-bar 
        :progress="progress"
        @playtrack="play" 
        @pausetrack="pause" 
        @stoptrack="stop">
      </player-controls-bar>
      <player-playlist-panel 
        :playlist="playlist" 
        :selectedTrack="selectedTrack" 
        @selecttrack="selectTrack" 
        @playtrack="play">
      </player-playlist-panel>
    </v-container>
  </div>
</template>

<script>
import CarouselHeader from "@/components/CarouselHeader.vue";
import PlayerTitleBar from "@/components/musicPlayer/PlayerTitleBar.vue"
import PlayerPlaylistPanel from "@/components/musicPlayer/PlayerPlaylistPanel.vue"
import PlayerControlsBar from "@/components/musicPlayer/PlayerControlsBar.vue"
import PlayerInfoPanel from "@/components/musicPlayer/PlayerInfoPanel.vue"
import {Howl} from 'howler'

export default {
  name: "Musica",
  components: {
    "carousel-header": CarouselHeader,
    "player-controls-bar": PlayerControlsBar,
    "player-playlist-panel": PlayerPlaylistPanel,
    "player-title-bar": PlayerTitleBar,
    "player-info-panel": PlayerInfoPanel
  },
  beforeDestroy: function () {
    this.stop();
  },
  created: function () {
    this.playlist.forEach( (track) => {
      let file = track.title.replace(/\s/g, "_")
      track.howl = new Howl({
        src: [`/assets/playlist/${file}.mp3`]
      })
    })
  },
  computed: {
    currentTrack () {
      return this.playlist[this.index]
    },
    getTrackInfo () {
      let artist = this.currentTrack.artist,
          title = this.currentTrack.title,
          seek = this.seek,
          duration = this.currentTrack.howl.duration()
      return {
        artist,
        title,
        seek,
        duration,
      }
    },
    progress () {
      if (this.currentTrack.howl.duration() === 0) return 0
      return this.seek / this.currentTrack.howl.duration()
    }
  },
  data: () => ({
    frase_musica: "Para que siempre escuches cosas lindas en tu vida",
    index: 0,
    playing: false,
    playlist: [
      {title: "Have It All", artist: "Jason Mraz", howl: null, display: true},
      {title: "Montón De Nada", artist: "Memphis La Blusera", howl: null, display: true},
      {title: "Cómo Te Extraño", artist: "Abel Pintos", howl: null, display: true},
      {title: "First Day Of My Life", artist: "Bright Eyes", howl: null, display: true},
      {title: "Creo Que Me Enamoré", artist: "Vicentico", howl: null, display: true},
      {title: "Please Forgive Me", artist: "Bryan Adams", howl: null, display: true},
      {title: "Orphans", artist: "Coldplay", howl: null, display: true},
      {title: "El Humahuaqueño", artist: "Soledad, Los Nocheros & Chaqueño Palavecino", howl: null, display: true},
      {title: "Cité Tango", artist: "Astor Piazzolla", howl: null, display: true},
      {title: "Catch And Release", artist: "Matt Simmons", howl: null, display: true}
    ],
    seek: 0,
    selectedTrack: null
  }),
  watch: {
    playing(playing) {
      this.seek = this.currentTrack.howl.seek()
      let updateSeek
      if (playing) {
        updateSeek = setInterval(() => {
          this.seek = this.currentTrack.howl.seek()
        }, 250)
      } else {
        clearInterval(updateSeek)
      }
    },
  },
  methods: {
    play (index) {
      let selectedTrackIndex = this.playlist.findIndex(track => track === this.selectedTrack)
    
      if (typeof index !== 'number') {
        if (this.selectedTrack) {
          if (this.selectedTrack != this.currentTrack) {
            this.stop()
          }
          index = selectedTrackIndex
        } else {
          index = this.index
        }
      }
    
      let track = this.playlist[index].howl
    
      if (track.playing()) {
        return
      } else {
        track.play()
      }
      
      this.selectedTrack = this.playlist[index]
      this.playing = true
      this.index = index
    },
    pause () {
      this.currentTrack.howl.pause()
      this.playing = false
    },
    selectTrack (track) {
      this.selectedTrack = track
    },
    stop () {
      this.currentTrack.howl.stop()
      this.playing = false
    }
  }
};
</script>
