<template>
  <div>
    <v-toolbar flat height=90 class="controls-bar">
      <v-spacer></v-spacer>
      <v-btn outline fab small @click="stopTrack" class="control-button">
        <v-icon>mdi-stop</v-icon>
      </v-btn>
      <v-btn outline fab @click="playTrack()" class="control-button">
        <v-icon large>mdi-play</v-icon>
      </v-btn>
      <v-btn outline fab small @click="pauseTrack" class="control-button">
        <v-icon>mdi-pause</v-icon>
      </v-btn>
      <v-spacer></v-spacer>
    </v-toolbar>
    <v-toolbar flat height="40" class="progress-bar-container">
      <v-progress-linear color="#F89FC1" class="progress-bar" v-model="trackProgress"></v-progress-linear> 
    </v-toolbar>
  </div>
</template>

<script>
export default {
  name: 'PlayerControlsBar',
  props: {
    playlist: Array,
    progress: Number,
    selectedTrack: Object
  },
  computed: {
    trackProgress () {
      return this.progress * 100
    },
  },
  methods: {
    playTrack(index) {
        this.$emit('playtrack', index)
    },
    pauseTrack() {
        this.$emit('pausetrack')
    },
    stopTrack() {
        this.$emit('stoptrack')
    }
  }
};
</script>

