<template>
  <v-card height="260" class="playlist-panel">
    <v-list class="playlist-tracklist">
      <v-list-tile 
        v-for="(track, index) in playlist" 
        :key="track.title" 
        v-show="track.display"
        class="playlist-track"
        :class="[{selected: track === selectedTrack}, {even: index % 2 == 0}]"
        @click="selectTrack(track)"
        @dblclick="playTrack()">
        <v-list-tile-content @dblclick="playTrack()">
          <v-list-tile-title>{{ index + 1 }} {{ track.artist }} - {{ track.title }}</v-list-tile-title>
        </v-list-tile-content>
        <v-list-tile-content class="track-duration">{{ track.howl.duration() | minutes }}</v-list-tile-content>
      </v-list-tile>
    </v-list>
  </v-card>
</template>
 
<script>
export default {
  name: 'PlayerPlaylistPanel',
  props: {
    playlist: Array,
    selectedTrack: Object
  },
  methods: {
    playTrack(index) {
      this.$emit('playtrack', index)
    },
    selectTrack (track) {
      this.$emit('selecttrack', track)
    }
  }
};
</script>

<style scoped>
.selected {
  background-color:  #EC407A !important;
}
.even {
  background-color: #757575;
}
.playlist-panel {
  background-color: #616161 !important;
  border-radius: 0px 0px 8px 8px !important;
}
.playlist-tracklist {
  background-color: #616161 !important;
}
.playlist-track {
  color: #F89FC1;
  cursor: pointer;
  display: block;
  font-size: 18px;
  font-weight: 600;
  height: 40px;
  padding-left: 10px;
  padding-right: 15px;
  padding-top: 7px;
}
.track-duration {
  float: right;
}
</style>