import Vue from 'vue'
import VueRouter from 'vue-router'
import Home from './views/Home'
import Perritos from './views/Perritos'
import CieloLuces from './views/CieloLuces'
import Flores from './views/Flores'
import Lugares from './views/Lugares'
import Musica from './views/Musica'
import DyG from './views/DyG'

Vue.use(VueRouter)

const routes = [
  {
    path: '/',
    name: 'Home',
    component: Home
  },
  {
    path: '/perritos',
    name: 'Perritos',
    component: Perritos
  },
  {
    path: '/cielo-luces',
    name: 'CieloLuces',
    component: CieloLuces
  },
  {
    path: '/flores',
    name: 'Flores',
    component: Flores
  },
  {
    path: '/lugares',
    name: 'Lugares',
    component: Lugares
  },
  {
    path: '/musica',
    name: 'Musica',
    component: Musica
  },
  {
    path: '/dyg',
    name: 'DyG',
    component: DyG
  }
]

const router = new VueRouter({
  mode: 'history',
  base: process.env.BASE_URL,
  routes
})

export default router
